.main{
    padding: 10px;
    height: 100vh;
    background-color: #D7E4C0;
}
.layout{
    display: flex;
}
.sidebar{
    min-height:100% ;
    width: 300px;
    border-radius: 5px;
    background-color:#006769;
    box-shadow: 0 0 2px grey;
    margin-right: 20px;
    color: white;
}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: #D7E4C0;
}
.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: #D7E4C0;
}
.logo h6{
    font-size:2rem;
    text-align: center;
    margin: 20px 10px;
}
.menu{
    margin-top:50px ;
}
.menu-item{
    cursor: pointer;
    margin-top: 30px;
}
.menu-item a{
    color:white;
    text-decoration: none;
    font-size: 1rem;
}
.menu-item i{
    font-size: 1rem;
    margin: 0 15px;
}
.active{
    background-color: white;
    padding: 10px;
}
.active a,.active i{
    color: #006769;
}

.header-content{
    display: flex;
    margin: 0 15px;
    align-items: center;
    justify-content: flex-end;
    height: 60px;
}

.header-content i{
    margin-right: 5px;
    font-size: 1.5rem;
}
.header-content a{
    text-decoration: none;
    font-weight: 500;
    color: #836FFF;
    font-size: 1.2rem;
    margin: 0 10px 0 40px;
    text-transform: uppercase;
}